import "./index.scss";

import React, { useState } from "react";

import {
  requestVerificationCode,
  validateLawyer,
  validateUser,
} from "../../../../services/registration.service";
import { Button, Checkbox, FileInput, TextInput } from "../../../atoms";
import { StepBackBlock } from "../../../organisms";

const MainInfoBlock = ({
  setCurrentStep,
  setChosenRegisterOption,
  chosenRegisterOption,
  formData,
  setFormData,
}) => {
  const [name, setName] = useState(formData?.get("name") || "");
  const [nameError, setNameError] = useState("");
  const [businessName, setBusinessName] = useState(
    formData?.get("businessName") || ""
  );
  const [businessNameError, setBusinessNameError] = useState("");
  const [phone, setPhone] = useState(formData?.get("phone") || "+48");
  const [phoneError, setPhoneError] = useState("");
  const [email, setEmail] = useState(formData?.get("email") || "");
  const [emailError, setEmailError] = useState("");
  const [NIP, setNIP] = useState(formData?.get("NIP") || "");
  const [NIPError, setNIPError] = useState("");
  const [password, setPassword] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [rePasswd, setRePasswd] = useState("");
  const [rePasswdError, setRePasswdError] = useState("");
  const [profilePicture, setProfilePicture] = useState();
  const [profileBackgroundPicture, setProfileBackgroundPicture] = useState();
  const [checkbox, setCheckbox] = useState(false);
  const [checkboxError, setCheckboxError] = useState(false);
  const [serverError, setServerError] = useState("");

  return (
    <div className="main-info-block">
      <form className="main-info-block--wrapper">
        <StepBackBlock
          title={"Podstawowe informacje"}
          handleOnClickBack={() => {
            setCurrentStep(0);
            setChosenRegisterOption(null);
          }}
        />

        {chosenRegisterOption === "DO_HELP" ? (
          <>
            <div className={"main-info-block--wrapper--grid"}>
              <TextInput
                value={name}
                error={nameError}
                setValue={e => {
                  setName(e.target.value);
                  setNameError("");
                }}
                label={"Imię i nazwisko*"}
              />
              <TextInput
                value={businessName}
                error={businessNameError}
                setValue={e => {
                  setBusinessName(e.target.value);
                  setBusinessNameError("");
                }}
                label={"Nazwa działalności gospodarczej*"}
              />

              <div className={"main-info-block--wrapper--grid--inner"}>
                <TextInput
                  value={phone}
                  type="tel"
                  setValue={e => {
                    setPhone(e.target.value);
                    setPhoneError("");
                  }}
                  label={"Numer telefonu*"}
                  error={phoneError}
                />
                <TextInput
                  value={email}
                  type="email"
                  setValue={e => {
                    setEmail(e.target.value);
                    setEmailError("");
                  }}
                  label={"E-mail*"}
                  error={emailError}
                />
              </div>

              <TextInput
                type="number"
                value={NIP}
                setValue={e => {
                  setNIP(e.target.value);
                  setNIPError("");
                }}
                label={"NIP*"}
                error={NIPError}
              />
            </div>
            <div
              className={"main-info-block--wrapper--grid--inner"}
              style={{ marginTop: 19 }}
            >
              <TextInput
                type="password"
                value={password}
                setValue={e => {
                  setPassword(e.target.value);
                  setPasswordError("");
                }}
                label={"Hasło*"}
                error={passwordError}
              />
              <TextInput
                type="password"
                value={rePasswd}
                setValue={e => {
                  setRePasswd(e.target.value);
                  setRePasswdError("");
                }}
                label={"Powtórz hasło*"}
                error={rePasswdError}
              />
            </div>

            <FileInput
              label={"Twoje zdjęcie profilowe"}
              additionalStyle={{ marginTop: 19 }}
              accept={"image/*"}
              setValue={setProfilePicture}
            />

            <FileInput
              label={"Twoje zdjęcie w tle"}
              additionalStyle={{ marginTop: 19 }}
              accept={"image/*"}
              setValue={setProfileBackgroundPicture}
            />
          </>
        ) : chosenRegisterOption === "NEED_HELP" ? (
          <>
            <TextInput
              value={name}
              error={nameError}
              setValue={e => {
                setName(e.target.value);
                setNameError("");
              }}
              label={"Imię i nazwisko*"}
            />
            <div
              className={"main-info-block--wrapper--grid--inner"}
              style={{ marginTop: 19 }}
            >
              <TextInput
                type="password"
                value={password}
                setValue={e => {
                  setPassword(e.target.value);
                  setPasswordError("");
                }}
                label={"Hasło*"}
                error={passwordError}
              />
              <TextInput
                type="password"
                value={rePasswd}
                setValue={e => {
                  setRePasswd(e.target.value);
                  setRePasswdError("");
                }}
                label={"Powtórz hasło*"}
                error={rePasswdError}
              />
            </div>

            <div
              className={"main-info-block--wrapper--grid--inner"}
              style={{ marginTop: 19 }}
            >
              <TextInput
                value={phone}
                type="tel"
                setValue={e => {
                  setPhone(e.target.value);
                  setPhoneError("");
                }}
                label={"Numer telefonu*"}
                error={phoneError}
              />
              <TextInput
                value={email}
                type="email"
                setValue={e => {
                  setEmail(e.target.value);
                  setEmailError("");
                }}
                label={"E-mail*"}
                error={emailError}
              />
            </div>
            <FileInput
              label={"Twoje zdjęcie profilowe"}
              additionalStyle={{ marginTop: 19 }}
              accept={"image/*"}
              setValue={setProfilePicture}
            />
          </>
        ) : null}
        <Checkbox
          setChecked={setCheckbox}
          label={"Akceptuję regulamin oraz politykę prywatności*"}
          error={checkboxError}
        />

        {serverError && (
          <p className="main-info-block--error-text">{serverError}</p>
        )}

        <Button
          text={"Przejdź dalej"}
          additionalStyle={{
            padding: "8px 0",
            width: 191,
            alignSelf: "center",
            marginTop: 52,
          }}
          handleOnClick={e => {
            e.preventDefault();
            const phoneRegex =
              /^(\b|\+48|48)\s*\d\s*\d\s*\d\s*\d\s*\d\s*\d\s*\d\s*\d\s*\d\s*$/;
            const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
            const strongPasswdRegex =
              /(?=^.{8,}$)(?=.*\d)(?=.*[!@#$%^&*]+)(?![.\n])(?=.*[A-Z])(?=.*[a-z]).*$/;
            console.log(phone);
            let anyError = false;

            if (name.length < 3) {
              setNameError("Zbyt krótkie imię. Podaj przynajmniej 3 znaki.");
              anyError = true;
            }
            if (!phone.replace(/\s/g, "").match(phoneRegex)) {
              setPhoneError("Nieprawidłowy numer telefonu");
              anyError = true;
            }
            if (!email.match(emailRegex)) {
              setEmailError("Nieprawidłowy email");
              anyError = true;
            }
            if (password !== rePasswd) {
              setRePasswdError("Hasła muszą być takie same");
              anyError = true;
            }
            if (!password.match(strongPasswdRegex)) {
              setPasswordError(
                "Hasło musi mieć minimum 8 znaków oraz przynajmniej: jeden symbol (!@#$%^&*), jedną wielką literę, jedną małą literę i jedną cyfrę"
              );
              anyError = true;
            }
            if (!checkbox) {
              setCheckboxError("Zaakceptuj regulamin i politykę prywatności");
              anyError = true;
            }
            if (chosenRegisterOption === "DO_HELP") {
              if (!validateNIP(NIP)) {
                setNIPError("Nieprawidłowy numer NIP");
                anyError = true;
              }
              if (businessName.length < 2) {
                setBusinessNameError(
                  "Zbyt krótka nazwa. Podaj przynajmniej 2 znaki"
                );
                anyError = true;
              }
            }

            if (anyError) return;

            const formData = new FormData();
            formData.append("name", name);
            formData.append("phone", phone.replace(/\s/g, ""));
            formData.append("email", email);
            formData.append("password", password);
            formData.append("profilePicture", profilePicture);
            if (chosenRegisterOption === "DO_HELP") {
              formData.append("NIP", NIP);
              formData.append("businessName", businessName);
              formData.append(
                "profileBackgroundPicture",
                profileBackgroundPicture
              );
            }
            setFormData(formData);
            setServerError("");

            if (chosenRegisterOption === "DO_HELP") {
              validateLawyer(formData).then(response => {
                if (response.success) {
                  setCurrentStep(1.5);
                  requestVerificationCode(phone);
                } else if (
                  response.data.error ===
                  "User with this email or phone already exists"
                ) {
                  setServerError(
                    "Użytkownik o podanym numerze telefonu lub adresie email już istnieje"
                  );
                } else {
                  setServerError("Wystąpił problem, spróbuj ponownie");
                }
              });
            } else {
              validateUser(formData).then(response => {
                if (response.success) {
                  setCurrentStep(2);
                  requestVerificationCode(phone);
                } else if (
                  response.data.error ===
                  "User with this email or phone already exists"
                ) {
                  setServerError(
                    "Użytkownik o podanym numerze telefonu lub adresie email już istnieje"
                  );
                } else {
                  setServerError("Wystąpił problem, spróbuj ponownie");
                }
              });
            }
          }}
        />
      </form>
    </div>
  );
};

export default MainInfoBlock;

const validateNIP = nip => {
  const nipWithoutDashes = nip.replace(/-/g, "");
  const reg = /^[0-9]{10}$/;
  if (reg.test(nipWithoutDashes) === false) {
    return false;
  } else {
    const digits = ("" + nipWithoutDashes).split("");
    const checksum =
      (6 * parseInt(digits[0]) +
        5 * parseInt(digits[1]) +
        7 * parseInt(digits[2]) +
        2 * parseInt(digits[3]) +
        3 * parseInt(digits[4]) +
        4 * parseInt(digits[5]) +
        5 * parseInt(digits[6]) +
        6 * parseInt(digits[7]) +
        7 * parseInt(digits[8])) %
      11;

    return parseInt(digits[9]) === checksum;
  }
};
