import axios from "axios";
import config from "../config";

export const requestVerificationCode = phoneNumber => {
  return axios
    .post(`${config.API_BASE}/auth/send-code`, { phone_number: phoneNumber })
    .catch(err => {
      console.log(err.message);
      console.log(err.response);
      console.log(err);
    });
};

export const validateUser = formData => {
  return axios
    .post(`${config.API_BASE}/auth/validate-user`, formData)
    .then(resp => {
      console.log(resp);
      return resp.data;
    })
    .catch(err => {
      console.log(err.response);
      return err.response;
    });
};

