import React, { useState } from "react";
import { StepBackBlock } from "../../../organisms";

import "./index.scss";
import { Button } from "../../../atoms";
import AuthInput from "../../../atoms/AuthInput";
import AuthInputGroup from "../../../molecules/AuthInputGroup";
import { useEffect } from "react";
import { createLawyer, createUser } from "../../../../services/registration.service";

const AuthBlock = ({ setCurrentStep, chosenRegisterOption, formData }) => {
  const [i1, setI1] = useState(0);
  const [i2, setI2] = useState(0);
  const [i3, setI3] = useState(0);
  const [i4, setI4] = useState(0);
  const [i5, setI5] = useState(0);
  const [i6, setI6] = useState(0);
  const [code, setCode] = useState();

  useEffect(() => {
    setCode([i1, i2, i3, i4, i5, i6].join(""));
  }, [i1, i2, i3, i4, i5, i6]);

  return (
    <div className="auth-block">
      <div className="auth-block--wrapper">
        <StepBackBlock
          title={"Autoryzacja"}
          handleOnClickBack={
            chosenRegisterOption === "DO_HELP"
              ? () => setCurrentStep(1.5)
              : chosenRegisterOption === "NEED_HELP"
              ? () => setCurrentStep(1)
              : null
          }
        />

        <p style={{ textAlign: "center" }}>Przesłaliśmy na Twój numer telefonu kod autoryzacyjny</p>

        <strong style={{ marginBottom: 57, marginTop: 50, textAlign: "center" }}>
          Wpisz otrzymany kod, aby przejść dalej:
        </strong>

        <AuthInputGroup setInputs={{ setI1, setI2, setI3, setI4, setI5, setI6 }} />

        <Button
          text={"Przejdź dalej"}
          additionalStyle={{
            padding: "8px 0",
            width: 191,
            alignSelf: "center",
            marginTop: 85,
          }}
          handleOnClick={() => {
            formData.set("code", code);
            if (chosenRegisterOption === "DO_HELP") {
              createLawyer(formData).then(() => {
                setCurrentStep(3);
              });
            } else {
              createUser(formData).then(() => {
                setCurrentStep(3);
              });
            }
          }}
        />
      </div>
    </div>
  );
};

export default AuthBlock;
