import React, { useState } from "react";
import { StepBackBlock } from "../../../organisms";

import "./index.scss";
import { Button } from "../../../atoms";
import { OptionChooser } from "../../../molecules";
import { createLawyer, getLawyerProfessions } from "../../../../services/registration.service";
import { useEffect } from "react";

const ProfessionBlock = ({ setCurrentStep, formData }) => {
  const [selectedOption, setSelectedOption] = useState();
  const [professions, setProfessions] = useState([]);

  useEffect(() => {
    getLawyerProfessions().then((professions) => setProfessions(professions));
  }, []);

  return (
    <div className="profession-block">
      <div className="profession-block--wrapper">
        <StepBackBlock
          title={"Wybierz swój zawód"}
          handleOnClickBack={() => {
            setCurrentStep(1);
          }}
        />

        <OptionChooser
          options={professions}
          selected={selectedOption}
          setSelected={setSelectedOption}
        />

        <Button
          text={"Przejdź dalej"}
          additionalStyle={{
            padding: "8px 0",
            width: 191,
            alignSelf: "center",
            marginTop: 85,
          }}
          handleOnClick={() => {
            formData.set("profession", selectedOption.id);
            setCurrentStep(2);
          }}
          disabled={selectedOption == null}
        />
      </div>
    </div>
  );
};

export default ProfessionBlock;
