import React, { useEffect, useState } from "react";
import { ProgressRegistration, RegistrationNavigation } from "../../organisms";
import ChooseBlock from "./ChooseBlock";
import ProfessionBlock from "./ProfessionBlock";
import MainInfoBlock from "./MainInfoBlock";
import AuthBlock from "./AuthBlock";
import LetsStartBlock from "./LetsStartBlock";

import "./index.scss";

const RegisterPage = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [chosenRegisterOption, setChosenRegisterOption] = useState();

  const [formData, setFormData] = useState();

  useEffect(() => {
    if (chosenRegisterOption) setCurrentStep(1);
  }, [chosenRegisterOption]);

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "space-between",
        flexDirection: "column",
      }}
    >
      <div>
        <div className="navigation-registration--block">
          <div className="container">
            <RegistrationNavigation />
          </div>
        </div>
        <div className="registration-choose--block">
          <div className="container">
            {currentStep === 0 ? (
              <ChooseBlock setChosenRegisterOption={setChosenRegisterOption} />
            ) : currentStep === 1 ? (
              <MainInfoBlock
                setCurrentStep={setCurrentStep}
                setChosenRegisterOption={setChosenRegisterOption}
                chosenRegisterOption={chosenRegisterOption}
                setFormData={setFormData}
                formData={formData}
              />
            ) : currentStep === 1.5 && chosenRegisterOption === "DO_HELP" ? (
              <ProfessionBlock setCurrentStep={setCurrentStep} formData={formData} />
            ) : currentStep === 2 ? (
              <AuthBlock
                setCurrentStep={setCurrentStep}
                chosenRegisterOption={chosenRegisterOption}
                formData={formData}
              />
            ) : currentStep === 3 ? (
              <LetsStartBlock
                setCurrentStep={setCurrentStep}
                chosenRegisterOption={chosenRegisterOption}
              />
            ) : null}
          </div>
        </div>
      </div>
      <div className="registration-progress--block">
        <div className="container">
          <ProgressRegistration
            progressNumber={currentStep}
            progress={["Kim jesteś?", "Podstawowe dane", "Autoryzacja", "Zaczynamy!"]}
            additionalStyle={{ maxWidth: 986, margin: "150px auto auto auto" }}
          />
        </div>
      </div>
    </div>
  );
};

export default RegisterPage;
