import React, { useRef } from "react";
import AuthInput from "../../atoms/AuthInput";

import "./index.scss";

const AuthInputGroup = ({ setInputs }) => {
  const refs = {
    ref1: useRef(),
    ref2: useRef(),
    ref3: useRef(),
    ref4: useRef(),
    ref5: useRef(),
    ref6: useRef(),
  };

  return (
    <div className="auth-input-group">
      <AuthInput
        reference={refs.ref1}
        handleOnChange={(e) => {
          e.nativeEvent.data && refs.ref2.current?.focus();
          setInputs.setI1(e.nativeEvent.data);
        }}
      />
      <AuthInput
        reference={refs.ref2}
        handleOnChange={(e) => {
          e.nativeEvent.data && refs.ref3.current?.focus();
          setInputs.setI2(e.nativeEvent.data);
        }}
      />
      <AuthInput
        reference={refs.ref3}
        handleOnChange={(e) => {
          e.nativeEvent.data && refs.ref4.current?.focus();
          setInputs.setI3(e.nativeEvent.data);
        }}
      />
      <AuthInput
        reference={refs.ref4}
        handleOnChange={(e) => {
          e.nativeEvent.data && refs.ref5.current?.focus();
          setInputs.setI4(e.nativeEvent.data);
        }}
      />
      <AuthInput
        reference={refs.ref5}
        handleOnChange={(e) => {
          e.nativeEvent.data && refs.ref6.current?.focus();
          setInputs.setI5(e.nativeEvent.data);
        }}
      />
      <AuthInput
        reference={refs.ref6}
        handleOnChange={(e) => {
          setInputs.setI6(e.nativeEvent.data);
        }}
      />
    </div>
  );
};

export default AuthInputGroup;
