import "./index.scss";

import React from "react";

import { useWindowDimensions } from "../../../../helpers/hooks";
import { Button } from "../../../atoms";
import { ChooseItem, StepBackBlock } from "../../../organisms";

const ChooseBlock = ({ setChosenRegisterOption }) => {
  const { width } = useWindowDimensions();

  return (
    <div className="choose-block">
      <div className="choose-block--wrapper">
        <StepBackBlock title={"Powiedz nam kim jesteś"} />

        <div className="choose-block-items">
          <div
            className="choose-block-item"
            onClick={() => {
              setChosenRegisterOption("NEED_HELP");
            }}
          >
            <ChooseItem
              text={"Potrzebuje pomocy"}
              link={require("../../../../assets/raster/woman.png").default}
            />

            {width <= 600 ? (
              <Button
                text={"Potrzebuję pomocy"}
                additionalStyle={{ height: 50, maxWidth: 300, zIndex: 100 }}
              />
            ) : null}
          </div>
          <div
            className="choose-block-item"
            onClick={() => {
              setChosenRegisterOption("DO_HELP");
            }}
          >
            <ChooseItem
              text={"Jestem doradcą"}
              link={require("../../../../assets/raster/user.png").default}
              isRight={true}
            />

            {width <= 600 ? (
              <Button
                text={"Jestem doradcą"}
                additionalStyle={{ height: 50, maxWidth: 300 }}
              />
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChooseBlock;
